/**
 * Component representing the chat header.
 */
import { Component, Prop, h, getAssetPath } from '@stencil/core';

@Component({
  tag: 'chat-header',
  styleUrl: 'chat-header.scss',
  shadow: true,
  assetsDirs: ['assets']
})
export class ChatHeader {
  /**
   * Indicates whether the chat is late or not.
   * @prop {boolean} late - Set to `true` if the chat is late, `false` otherwise. Defaults to `false`.
   */
  @Prop() late: boolean = false;

  /**
   * The name displayed in the chat header.
   * @prop {string} name - The name to be displayed. Defaults to 'Vivi'.
   */
  @Prop() name: string = 'Vivi';

  render() {
    return (
      <div>
        <div class="dav__header">
          <div class="dav__header__left">
            <app-button classButton="backVivi">
              <img src={getAssetPath(`../assets/icons/default-icons/icon-arrow-back.svg`)} />
            </app-button>
            <div class="dav__header__left__icon">
              <img src={getAssetPath(`../assets/icons/extra-icons/icon-vivi-assistant.svg`)} />
            </div>
            <p class="dav__header__left__name">
              {this.name || 'Vivi'}
            </p>
          </div>
        </div>
      </div>
    );
  }
}
